<template>
  <div class="container">
    <form @submit.prevent="submitForm" class="container-caeser">
      <h1 class="title">Cifrado de Escítala</h1>

      <div class="form-group">
        <label for="message" class="form-label"> Mensaje a cifrar: </label>
        <input
          v-model="message"
          id="message"
          type="text"
          autocomplete="off"
          placeholder="Hola como estas"
          class="form-control"
          :class="{ 'form-error': $v.message.$invalid && message.length }"
        />
        <span
          :class="{ 'text-error': $v.message.$invalid }"
          v-show="$v.message.$invalid && message.length"
        >
          Solo se permiten letras (A-Z y a-z)
        </span>
      </div>

      <div class="form-group">
        <label for="shift" class="form-label">
          Número de columnas: {{ columns }}
        </label>
        <input
          v-model="columns"
          type="range"
          min="1"
          :max="message.length"
          id="shift"
          class="form-slider"
        />
      </div>

      <div class="form-group btn-group">
        <button
          @click="encrypt"
          class="btn btn-primary"
          :class="{ disabled: $v.message.$invalid }"
          :disabled="$v.message.$invalid"
        >
          Cifrar
        </button>
        <button @click="decrypt" class="btn btn-texted">Descifrar</button>
      </div>
    </form>
    <div class="container-answer" v-show="result.length">
      <h3 class="title">Matriz generada:</h3>
      <hr class="divider" />
      <table>
        <tbody>
          <tr v-for="(row, rowIndex) in tableMatrix" :key="rowIndex">
            <td v-for="(char, colIndex) in row" :key="colIndex">
              {{ char || " " }}
            </td>
          </tr>
        </tbody>
      </table>
      <hr class="divider" />
      <h3 class="title">Texto cifrado:</h3>
      <p class="text-result">
        {{ result }}
      </p>
      <hr class="divider" />
      <button class="btn btn-texted btn-texted--link" @click="copyText">
        Copiar mensaje
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from "vue";
import Swal from "sweetalert2";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";

const message = ref("");
const columns = ref(2);
const result = ref("");
const tableMatrix = ref([]);

const isButtonEnabled = computed(() => message.value.length >= 2);

const decrypt = () => {
  let mensajeDescifrado = "";

  // Recorremos la matriz por filas y reconstruimos el mensaje original
  for (let row = 0; row < tableMatrix.value.length; row++) {
    for (let col = 0; col < tableMatrix.value[row].length; col++) {
      if (tableMatrix.value[row][col]) {
        mensajeDescifrado += tableMatrix.value[row][col];
      }
    }
  }

  result.value = mensajeDescifrado;
};

const encrypt = () => {
  const rows = Math.ceil(message.value.length / columns.value);
  let encryptedMessage = "";
  let matrix = Array.from({ length: rows }, () =>
    Array(columns.value).fill("")
  );

  // Recorremos las columnas y construimos el mensaje cifrado
  for (let col = 0; col < columns.value; col++) {
    for (let row = 0; row < rows; row++) {
      const index = row * columns.value + col;
      if (index < message.value.length) {
        encryptedMessage += message.value[index];
        matrix[row][col] = message.value[index];
      }
    }
  }

  result.value = encryptedMessage;
  tableMatrix.value = matrix;
};

const alphaWithSpaces = (value) => {
  return /^[a-zA-Z\s]*$/.test(value); // Permite letras y espacios
};

const rules = {
  message: {
    required,
    alphaWithSpaces,
  },
};

const $v = useVuelidate(rules, { message });

const submitForm = async () => {
  if ($v.value.$invalid) {
    $v.value.$touch();
    return;
  }
};

const copyText = () => {
  navigator.clipboard.writeText(result.value);
  Swal.fire({
    title: "Copiado correctamente",
  });
};
</script>

<style>
/* Estilos para la tabla */
table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
}

td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: center;
  font-size: 16px;
}
</style>
